export const seriesLabelFormConfig = () => [
  {
    type: 'row',
    text: '显示数值',
    items: [
      {
        name: 'showSeriesLabel',
        span: 6,
        type: 'switch',
      },
      {
        display: (_: any, { model }: any) => model.showSeriesLabel,
        name: 'seriesLabelFontSize',
        placeholder: '大小',
        type: 'number',
        min: 0,
        span: 6,
      },
      {
        display: (_: any, { model }: any) => model.showSeriesLabel,
        name: 'seriesLabelColor',
        type: 'colorPicker',
        span: 6,
      },
    ],
  },
];
export const seriesLabelValue = () => ({
  showSeriesLabel: false,
  seriesLabelFontSize: 12,
  seriesLabelColor: '#0f0f0f',
});
export const seriesLabelOption = (config: any, opts?: any) => {
  if (opts?.isBar) {
    if (config.showSeriesLabel) {
      const { value, orient } = opts;
      const isHorizontal = orient === 'horizontal';
      let position = 'top';
      if (isHorizontal) {
        position = value < 0 ? 'left' : 'right';
      } else {
        position = value < 0 ? 'bottom' : 'top';
      }
      return {
        show: true,
        position,
        fontSize: config.seriesLabelFontSize,
        color: config.seriesLabelColor,
      };
    }
    return;
  }
  return config.showSeriesLabel
    ? {
        show: true,
        position: 'top',
        fontSize: config.seriesLabelFontSize,
        color: config.seriesLabelColor,
      }
    : undefined;
};
